.nav__list {
  @include list-reset;
  column-count: 2;

  &--contacts {
    column-count: 1;
  }
}

.nav__list li {
  margin-right: 60px;
}

.nav__list--contacts>li {
  margin-right: 0;
}

.nav__item,
.nav__link {
  color: rgba(98, 105, 98, 0.5);
  font-size: 15px;
  line-height: 26px;

  &:hover,
  &:focus {
    color: #1b2d37;
  }
}

@media (max-width: $tablet) {
  .nav {
    padding: 40px 0 8px 25px;
    &--contacts {
      padding: 40px 25px 8px 0;
    }
  }
  .nav__list {
    column-count: auto;
  }

  .nav__list li {
    margin-right: 0;
  }
}

@media (max-width: $mobile) {
  .nav {
    width: 100%;
    padding-top: 22px;
    padding-left: 14px;
    padding-right: 15px;
    padding-bottom: 15px;
    border: 1px solid rgba(98, 105, 98, 0.2);;
  }

  .nav__list {
    padding: 6px 2px 0;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
  }

}
